body.basic-main-layout {
  background-color: #272c30;

  //No top nav section:
  .page-content {
    //margin-top: -68px !important;
    min-height: 100vh !important;
  }

  .basic_page-content {
    h3.page-title {
      background: #fff;
      margin-top: -25px;
      margin-left: -20px;
      margin-right: -20px;
      padding: 17px 20px;
      color: #000;
      font-weight: 500;
      font-size: 20px;
      border-bottom: 1px solid #E4E8EB;
    }
  }
  .btn {
    &.btn-pink {
      color: #fff;
      background-color: #ff6784;
      border-color: #ff6784;
      &:hover {
        background-color: #ff7b94;
        border-color: #fb7f97;
      }
      &.active {
        background-color: #ea617c;
        border-color: #f3778f;
      }
    }
  }
  .bootstrap-switch {
    .bootstrap-switch-handle-on {
      &.bootstrap-switch-success {
        color: #fff;
        background: #ff6784;
        &:hover {
          background-color: #ff7b94;
          border-color: #fb7f97;
        }
      }
    }
    .bootstrap-switch-handle-off {
      &.bootstrap-switch-success {
        color: #fff;
        background: #ff6784;
        &:hover {
          background-color: #ff7b94;
          border-color: #fb7f97;
        }
      }
    }
  }

  .tabbable-line>.tab-content {
    border: 0;
    padding: 0;
  }

  .portlet.light .portlet-body {
    padding-top: 0;
    padding-bottom: 10px;
  }
}